.search-input {
  flex: 50% 0 1;
  flex-grow: 1;
  margin-right: 3px;
  text-align: center;
}
@media screen and (max-width: 420px){
  .top-panel {
    flex-direction: column;
  }
  .search-input {
    width: 100%;
    margin-bottom: 0.5rem;
    border-color: rgb(108, 117, 125);
  } 
}