@import url(https://fonts.googleapis.com/css2?family=Cousine&family=Fira+Mono&display=swap);
.app-header {
  align-items: flex-end;
}

.app-header h1 {
  flex-grow: 1;
}

.app-header h2 {
  font-size: 1.2rem;
  color: var(--additionalTextColor);
}

@media screen and (max-width: 420px) {
  .app-header {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.search-input {
  flex: 50% 0 1;
  flex-grow: 1;
  margin-right: 3px;
  text-align: center;
}
@media screen and (max-width: 420px){
  .top-panel {
    flex-direction: column;
  }
  .search-input {
    width: 100%;
    margin-bottom: 0.5rem;
    border-color: rgb(108, 117, 125);
  } 
}
.todo-list-item {
  font-size: 1.25rem;
}

.todo-list-item button {
  width: 35px;
  margin: 3px;
}

.todo-list-item-label {
  margin-left: 0.5rem;
  line-height: 35px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.todo-list-item.done .todo-list-item-label {
  text-decoration: line-through;
}

.todo-list-item.important .todo-list-item-label {
  font-weight: bold;
  color: steelblue;
}

@media (max-width: 420px) {
  .todo-list-item-label {
    display: block;
  }
}

.todo-list .list-group-item {
  padding: .25rem .75rem;
}
.btn-group {
  flex: 50% 0 1;
}
.add-item-panel {
  margin: 1rem 0;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}
.add-item-panel input {
  width: auto;
  flex-grow: 1;
  margin-right: 3px;
  color: var(--additionalTextColor);
}

@media (max-width: 420px) {
  .add-item-panel {
    flex-wrap: wrap;
  }
  .add-item-panel .btn {
    flex-basis: 100%;
    margin-top: 0.5rem;
  }

}
.todo-app {
  margin: 2rem auto 0 auto;
  max-width: 400px;
}
.top-panel {
  margin: 1rem 0;
}
@media screen and (max-width: 420px) {
  .todo-app {
    max-width: 90%;
  }
}
* {
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Mono', monospace;
  --primaryBackgroundColor: #DBFCFF;
  --primaryTextColor: #1F2041;
  --additionalTextColor: #857E61;
}

@media (prefers-color-scheme: dark) {
  html {
    --primaryBackgroundColor: #1f2041;
    --primaryTextColor: #dbfcff;
  }   
}

body {
  background-color: var(--primaryBackgroundColor);
  color: var(--primaryTextColor);
}

.btn-outline-secondary {
  border-color: var(--additionalTextColor);
  color: var(--additionalTextColor);
}

.form-control {
  border-color: var(--additionalTextColor);
  color: var(--additionalTextColor);
}


