.app-header {
  align-items: flex-end;
}

.app-header h1 {
  flex-grow: 1;
}

.app-header h2 {
  font-size: 1.2rem;
  color: var(--additionalTextColor);
}

@media screen and (max-width: 420px) {
  .app-header {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}